import React from 'react';

import Box from '@mui/material/Box';
import Grid from '@mui/material/Unstable_Grid2';
import Avatar from '@mui/material/Avatar';

import ProfileMessage from './components/ProfileMessage';

import './css/common.css';
import './css/Profile.css';

import AvatarImage from './media/avatar.jpg';
// import { Typography } from '@mui/material';

const Profile: React.FC = () => {
  return (
    <Box className="mainContent">
      <Box className="title">Profile</Box>
      <Box className="content">
        <Box className="profile">
          <Grid container spacing={2}>
            <Grid xs={10} mdOffset="auto">
              <ProfileMessage
                message1="Web"
                message2="Java, C"
                message3="IT10"
              />
            </Grid>
            <Grid xs={10} mdOffset="auto">
              <ProfileMessage
                message1="AWS"
                message2="Docker"
                message3="IT"
              />
            </Grid>
            <Grid xs={10} mdOffset="auto">
              <ProfileMessage
                message1=""
                message2=""
                message3=""
              />
            </Grid>
            <Grid xs={10} mdOffset="auto"></Grid>
          </Grid>
          <Grid container spacing={2}>
            <Grid xs={1} mdOffset="auto">
              <Avatar
                alt="Tama"
                src={AvatarImage}
                sx={{ width: 100, height: 100 }}
              />
            </Grid>
            <Grid xs={9}>
              <Grid xs={12}>
                <Box>
                  Twitter:
                  <a
                    href="https://twitter.com/tama_nattsu/"
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    tama_nattsu
                  </a>
                </Box>
              </Grid>
              <Grid xs={12} mdOffset="auto">
                <Box>
                  Instagram:
                  <a
                    href="https://www.instagram.com/tama_katsu13/"
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    tama_katsu13
                  </a>
                </Box>
              </Grid>
              <Grid xs={12} mdOffset="auto">
                <Box>
                  Github:
                  <a
                    href="https://github.com/k12-tamaki/"
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    k12-tamaki
                  </a>
                </Box>
              </Grid>
            </Grid>
          </Grid>
        </Box>
      </Box>
    </Box>
  );
};

export default Profile;
