import React from 'react';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Unstable_Grid2';
import Divider from '@mui/material/Divider';

const IoT: React.FC = () => {
  return (
    <>
      <Grid container spacing={2}>
        <Grid xs={7}>
          <Box>
            <img src="/static/website_normal.png" />
          </Box>
        </Grid>
        <Grid xs={5}>
          <Typography align="center" variant="h4" sx={{ padding: '20px' }}>
            IoT
          </Typography>
          <Divider />
          <Typography>
            IoT
          </Typography>
          <Typography>
            RaspberryPi(RevPi)AWS
            highchartsWeb
            
          </Typography>
          <Typography>
            Mailgun
          </Typography>
          <Typography>
            RaspberryPi(RevPi)
          </Typography>
          <Typography
            align="center"
            variant="h4"
            sx={{ padding: '20px', marginTop: '50px' }}
          >
            
          </Typography>
          <Divider />
          <Typography>
            AWS(EC2, RDS, S3), Python, RaspberryPi(RevPi),
          </Typography>
          <Typography>WebSocket, Vue, highcharts and more...</Typography>
        </Grid>
      </Grid>
    </>
  );
};

export default IoT;
